
import "../css/admin.css";


import { Bar, Pie, Line, Doughnut, Radar, PolarArea, Scatter } from "react-chartjs-2";
import Chart from 'chart.js/auto';
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom"
import { useSelector, useDispatch } from "react-redux/es/exports";



import { set_user, setting_charts } from "../store/counterslice"
import { auth } from "../config/firebase.js"
import { GoogleAuthProvider } from "firebase/auth"
import { signInWithPopup } from "firebase/auth"






const App = () => {

  const navigate = useNavigate()
  const dispatch = useDispatch()

  const count = useSelector(state => state.counter)







  const google_login = () => {






    const provider = new GoogleAuthProvider();
    signInWithPopup(auth, provider)
      .then((result) => {



        const credential = GoogleAuthProvider.credentialFromResult(result);
        const token = credential.accessToken;

        const user = result.user;


        const obj = { username: user.displayName, photoURL: user.photoURL, uid: user.uid }


        dispatch(set_user(obj))




      }).catch((error) => {

        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage)
        // const email = error.email;

        // const credential = GoogleAuthProvider.credentialFromError(error);

      });





  }








  const headers = {
    'Content-Type': 'application/json;charset=UTF-8',
    "Access-Control-Allow-Origin": "*",
    'Access-Control-Allow-Methods': 'GET, PUT, POST, DELETE, OPTIONS',
    'Access-Control-Allow-Headers': '*'
  }



  useEffect(() => {

    data_lao()
  }, [1000])





  const data_lao = async () => {
    fetch("https://quiz-ser.herokuapp.com/get-result", {
      method: "GET",
      headers: headers
    })

      .then(r => r.json())
      .then(result => dispatch(setting_charts(result)))
  }









  const [userdata, setuserdata] = useState({


    labels: count.names,

    datasets: [{
      label: "num of votes", data: count.score, backgroundColor: ["orange", "yellow", "pink", "green", "green"], barPercentage: 0.5,
      barThickness: 20,

      tension: 0.4,

    }]
  })





  return (

    <div className="base">


      <div className="left">


        <div className="left-part left-one"> <img referrerPolicy="no-referrer" src="https://img.icons8.com/ios/50/000000/canvas-student--v1.png" className="logo" /> </div>
        <div className="left-part left-two"> <img referrerPolicy="no-referrer" onClick={() => google_login()} src={count.current_user.photoURL} className="login_img" /> </div>
        <div className="left-part left-three" onClick={() => navigate("/quiz")}>Quiz</div>
        <div className="left-part left-four" onClick={() => navigate("/quiz/admin")}>Admin</div>
        <div className="left-part left-four" onClick={() => navigate("/quiz/result")}>Result</div>

        <div className="left-part logout">Logout</div>




      </div>






      <div className="right">



        <div className="part one"></div>



        <div className="part two">
          <Bar
            className="actual_bar"
            data={userdata}



            height='50vh'
            width='50vw'
            options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 } } } }}

          />


        </div>



        <div className="part three">

          <Doughnut
            className="actual_bar"
            data={userdata}




            options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 } } } }}

          />






        </div>






        <div className="part four">


          <Pie
            className="actual_bar"
            data={userdata}



            height='50vh'
            width='50vw'
            options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 } } } }}

          />


        </div>




        <div className="part five">
          <PolarArea
            className="actual_bar"
            data={userdata}



            height='50vh'
            width='50vw'
            options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 } } } }}

          />



        </div>






        <div className="part six">



          <Line
            className="actual_bar"
            data={userdata}





            options={{ maintainAspectRatio: false, plugins: { legend: { labels: { font: 200 } } } }}

          />


        </div>




      </div>


    </div>
  )
}




export default App